import React from "react";
import "./styles.css";

function Footer() {
  return (
    <div className="footer">
      <div className="floatrighttypeformbutton">
        <div className="poweredbyclass">
          <button className="buttonClass typeformButton ">
            <span className="poweredBybutton">
              {" "}
              Powered by <span className="spanbolder">Typeform</span>
            </span>{" "}
          </button>
          {/* <!-- TODO onclick of the button, redirect to typeform website --> */}
        </div>
      </div>
      <div className="footerfloatright">
        <div className="upDownClass">
          <div>
            <button className="buttonClass footerButtons upbutton">
              <i className="bi-chevron-up"></i>
            </button>
            {/* <!-- Onclick of up button, replace existing component with previous component(option,label, input) --> */}
          </div>
          <div className="spacebetweenupdownbutton"></div>
          <div>
            <button className="buttonClass footerButtons downbutton">
              <i className="bi-chevron-down"></i>
            </button>
            {/* <!-- Onclick of down button, replace existing component with next component(option,label, input) --> */}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Footer;
