import React from "react";
import "./styles.css";

function Header() {
  return (
    <div className="header">
      <div className="leftofleftheader"></div>
      {/* the title number goes here inplace of '1' */}
      <div className="headerleft">1</div>
      <i className="bi-arrow-right-short"></i>
      {/* Title of the page in header goes in here inplace of 'Tax Team Contact Details' */}
      <div className="headerright">Tax Team Contact Details</div>
    </div>
  );
}
export default Header;
