import React from "react";
import "./styles.css";

function Form() {
  return (
    <div className="formcontainer">
      <form>
        <div className="group">
          <div className="leftofleftchild"></div>
          <div className="leftchild">
            <div className="option">
              <label className="optionlabel">a.</label>
              {/* inplace of 'a' replace it with the option of the question */}
            </div>
          </div>
          <div className="rightchild">
            <div className="labelname">
              {/*  label name goes here inplace of 'Name of the Tax Team SPOC' */}
              <label>Name of the Tax Team SPOC</label>
            </div>
            <div className="inputspace">
              <input
                type="text"
                className="input-style"
                placeholder="Type your answer here..."
              />
            </div>
            <div className="buttongroup">
              {/* onclick of OK button, replace existing component with next component(option,label, input)  */}
              <button className="buttonClass OKbutton">
                <span className="buttonspan">OK</span>
                <i className="bi-check2"></i>
              </button>

              <span className="NextToOkspan">
                press <span className="spanbolder">Enter â†µ</span>{" "}
              </span>
            </div>
          </div>
          <div className="rightofrightchild"></div>
        </div>
      </form>
    </div>
  );
}

export default Form;
